@-webkit-keyframes animStar {
  from {
      transform: translateY(0px);
  }
  to {
      transform: translateY(-2000px);
      
  }
}


@keyframes animStar {
  from {
      transform: translateY(0px);
  }
  to {
      transform: translateY(-2000px);
      
  }
}
.navbar{
    z-index: 30;
    font-family: 'Baloo Tammudu 2', cursive;
    padding-top: 30px;
    
    width: 100%;
    height: 60px;
    
}
Nav{
    font-size: 20px;
    
}

.home-context{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    
    
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    font-family: 'Baloo Tammudu 2', cursive;
}


.icon{
    border-style: solid;
    border-width: 5px;
    border-radius: 50%;
    width: 280px;
    height: 280px;
    border-color: #FFFFFF;
    margin: 20px;
    box-shadow:2px 2px 5px darkblue;
      
}
.header-home{
    font-size: 70px;
    color:  white;
    text-shadow: 2px 2px 5px darkblue;
    
}
.text-home{
    font-size: 25px;
    color: white;
    text-align: left;
    text-shadow: 2px 2px 5px darkblue;
}

.project {
    font-family: 'Baloo Tammudu 2', cursive;
    color: white;
    width: 100%;
    height: 100%;
}
.projects-list{
    width: 1200px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding-top: 100px;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    
    
}


.profile {
    font-family: 'Baloo Tammudu 2', cursive;
    color: white;
    width: 100%;
    height: 100%;
}
.profile-content{
    width: 1100px;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding-top: 100px;
    display: flex;
    flex-flow: column;
    
     
}

.profile-top{
    height: 450px;
    display: flex;
    flex-flow: row;      
}
.profile-left{
    height: 100%;  
    width: 40%;
}
.profile-right{
    height: 100%;  
    width: 60%;
    padding: 50px;
    text-align: left;
    
}
.profile-bot{
    height: 600px;
    width: 100%;
    border-radius: 20px;
    border-style: solid;
    border-width: 5px;
    border-color: #FFFFFF;
     
}

.profile-img{
    width: 350px;
    height: 350px;
    margin: 50px;
    border-radius: 20px;
    border-style: solid;
    border-width: 5px;
    border-color: #FFFFFF;
    box-shadow:5px 5px 10px darkslateblue;

}

.text-header{
    font-size: 50px;
}

.text{
    font-size: 30px;
    padding-left: 30px;
}
